export default {
  data() {
    return {
      items: [
        {
          name: '首页',
          url: '/',
          icon1: '#icon-index1',
          icon2: '#icon-index0'
        },
        {
          name: '个人中心',
          url: '/mine',
          icon1: '#icon-mine1',
          icon2: '#icon-mine0'
        }
      ]
    }
  },
  methods: {
    toUrl(i) {
      this.$router.replace({
        path: this.items[i].url
      })
    }
  }
}



